import React from 'react';
import { getSmoothStepPath } from 'reactflow';

export default function CustomEdge({
    id,
    sourceX,
    sourceY,
    targetX,
    targetY,
    sourcePosition,
    targetPosition,
    style = {},
    markerEnd
}) {
    const [edgePath] = getSmoothStepPath({ sourceX, sourceY, sourcePosition, targetX, targetY, targetPosition });

    return (
        <path
            id={id}
            style={style}
            className="react-flow__edge-path custom" // Added custom class for styling
            d={edgePath}
            markerEnd={markerEnd}
        />
    );
}